import React from 'react';
import { graphql, navigate } from 'gatsby';
import { GatsbySeo } from 'gatsby-plugin-next-seo';
import Img from 'gatsby-image';

import Layoout from '../components/Layout';
import Container from '../components/Container';
import Banner from '../components/Banner';
import BlockHeaderNavSEMLP from '../components/BlockHeaderNavSEMLP';

import BlockHeaderNoImage from '../components/BlockHeaderNoImage';
import SpaceDivider from '../components/SpaceDivider';
import BlockLogos from '../components/BlockLogos';

import Block4Part from '../components/Block4Part';
import Block4PartCompanies from '../components/Block4PartCompanies';
import Footer from '../components/Footer';

import { edgesByName } from '../utils/gql';
import Button from '../components/Button';

export const pageQuery = graphql`
  {
    simpleTire: file(name: { eq: "simple-tire" }) {
      childImageSharp {
        fluid(maxWidth: 520) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    simpleTireSmall: file(name: { eq: "simple-tire-small" }) {
      childImageSharp {
        fluid(maxWidth: 244) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    companies: allContentfulCompany {
      edges {
        node {
          name
          image {
            fluid(maxWidth: 150) {
              ...GatsbyContentfulFluid
            }
          }
        }
      }
    }
    OndemandTiresImage: contentfulCompany(name: { eq: "Ondemand tires" }) {
      image {
        fixed(width: 130) {
          ...GatsbyContentfulFixed
        }
      }
    }
    LEDMyplaceImage: contentfulCompany(name: { eq: "LEDMyplace" }) {
      image {
        fixed(width: 130) {
          ...GatsbyContentfulFixed
        }
      }
    }
    ikioImage: contentfulCompany(name: { eq: "ikio" }) {
      image {
        fixed(width: 130) {
          ...GatsbyContentfulFixed
        }
      }
    }
  }
`;

export default function SEMLandingPage({ data }) {
  const companies = edgesByName(data.companies, node => node.image.fluid);
  function handleContactUsClick() {
    navigate('/contact-sales');
  }
  return (
    <Layoout>
      <GatsbySeo title="Advance Pay" />
      <Banner />
      <Container>
        <BlockHeaderNavSEMLP />
        <BlockHeaderNoImage
          title="Net 30/60/90 day payment terms with Resolve"
          desc="Grow your b2b sales quickly by offering your customers net payment terms"
          primaryButtonText="Contact our Sales Team Today"
          primaryButtonTo="/contact-sales"
        />

        <SpaceDivider height={100} />
        <Img
          fluid={data.simpleTire.childImageSharp.fluid}
          className="main-image"
          imgStyle={{ objectFit: 'cover', objectPosition: 'top center' }}
        />
        <SpaceDivider height={100} />

        <BlockLogos
          data={[
            {
              image: companies['CozyEarth'],
              name: 'CozyEarth',
              offsetTop: 0,
            },
            {
              image: companies['Hyperikon'],
              name: 'Hyperikon',
            },
            {
              image: companies['Interior Define'],
              name: 'Interior Define',
              offsetTop: 0,
            },
            {
              image: companies['SimpleTire'],
              name: 'SimpleTire',
              offsetTop: 0,
            },
            {
              image: companies['Real Thread'],
              name: 'Real Thread',
              offsetTop: -6,
            },
          ]}
        />

        <SpaceDivider height={100} />

        <section className="sem-lp__section">
          <div className="app__row">
            <div className="app__column">
              <h3 className="app__block_title">
                Getting set up is a easy as 1, 2, 3
              </h3>
              <p className="app__block_desc">
                get set up within minutes of speaking to a member of our sales
                team
              </p>
            </div>
          </div>
        </section>

        <Block4Part
          items={[
            {
              title: 'Improve Cashflow',
              content: <div>Description</div>,
              image: companies['CozyEarth'],
            },
            {
              title: 'Optimize Accounts Receivable',
              content: <div>Description</div>,
              image: companies['CozyEarth'],
            },
            {
              title: 'Grow your Sales',
              content: <div>Description</div>,
              image: companies['CozyEarth'],
            },
          ]}
        />

        <SpaceDivider height={100} />

        <Button
          hasArrow
          primary
          onClick={handleContactUsClick}
          className="sem-lp__contact-us-button"
        >
          Contact our Sales Team Today
        </Button>

        <SpaceDivider height={100} />

        <Block4PartCompanies
          title="Innovative companies see results with Resolve"
          mainItem={{
            image: data.OndemandTiresImage.image.fixed,
            imageDesc: 'Ondemand tires company logo',
            review: (
              <span>
                "When a customer of ours uses Resolve,
                <br />
                they're significantly more likely to buy from
                <br />
                us again and again down the road."
              </span>
            ),
          }}
          items={[
            {
              id: 'led-my-place',
              image: data.LEDMyplaceImage.image.fixed,
              imageDesc: 'LEDMyplace company logo',
              review: '20% Increase in average order size',
            },
            {
              id: 'ikio',
              image: data.ikioImage.image.fixed,
              imageDesc: 'ikio company logo',
              review: 'Increase in corporate buyers',
            },
          ]}
        />
        <SpaceDivider height={100} />

        <section className="sem-lp__section">
          <div className="app__row">
            <div className="app__column">
              <h3 className="app__block_title">
                Join some of the world's most innovative <br />
                companies who trust Resolve
              </h3>
            </div>
          </div>

          <Button
            hasArrow
            primary
            onClick={handleContactUsClick}
            className="sem-lp__contact-us-button"
          >
            Contact our Sales Team Today
          </Button>
        </section>
      </Container>
      <Footer />
      <style jsx global>{`
        .sem-lp__section {
          text-align: center;
        }

        .sem-lp__contact-us-button {
          display: block;
          margin: 0 auto;
        }

        .main-image {
          max-height: 300px;
        }
      `}</style>
    </Layoout>
  );
}
